@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#test-card {
  width: 200px;
  height: 200px;
}

.showCase {
  width: 400px;
}

.margin-normal {
  margin: 16px;
}

.text-size-2 {
  font-size: 1.2rem;
}

.text-color-primary {
  color: #282c34;
}

.text-font-bold {
  font-weight: bold;
}

.margin-xs {
  margin: 4px;
}

.margin-2 {
  margin: 12px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  flex-wrap: wrap;
}

.flex-row > * {
  margin-right: 8px;
}
@-webkit-keyframes kf-flip-first-half {
    from {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 0deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 0deg);
        box-shadow: 2px 2px 5px #666;
    }
    to {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
        box-shadow: -3px 2px 5px #666;
    }
}
@keyframes kf-flip-first-half {
    from {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 0deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 0deg);
        box-shadow: 2px 2px 5px #666;
    }
    to {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
        box-shadow: -3px 2px 5px #666;
    }
}
@-webkit-keyframes kf-flip-second-half {
    from {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg) rotateY(180deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 90deg) rotateY(180deg);
        box-shadow: 2px 2px 5px #666;
    }
    to {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 180deg) rotateY(180deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 180deg) rotateY(180deg);
        box-shadow: -3px 2px 5px #666;
    }
}
@keyframes kf-flip-second-half {
    from {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg) rotateY(180deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 90deg) rotateY(180deg);
        box-shadow: 2px 2px 5px #666;
    }
    to {
        -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 180deg) rotateY(180deg);
                transform: perspective(400px) rotate3d(0, 1, 0, 180deg) rotateY(180deg);
        box-shadow: -3px 2px 5px #666;
    }
}

